import React from "react";
import styles from "./RdvButton.module.css";

const RdvButton = () => {
  const handleRedirect = () => {
    window.open("https://app.my-orthoadvance.com/#/cabinets/lucas", "_blank");
  };

  return (
    <button className={styles.urgentLink} onClick={handleRedirect}>
      Rendez-vous
    </button>
  );
};

export default RdvButton;
